import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const INGREDIENTS_PRICES = {
    salad: 0.5,
    bacon: 0.7,
    cheese: 0.4,
    meat: 1.3
}

const initialState = {
    ingredients: null,
    price: 4,
    error: false,
    building: false
}

const initIngredients = (state, action) => {
    return updateObject(state, {
        ingredients: null,
        price: 4,
        error: false
    });
}

const addIngredient = (state, action) => {
    const updatedIngredientsAdded = updateObject(state.ingredients, {
        [action.igType]: state.ingredients[action.igType] + 1
    });
    return updateObject(state, {
        ingredients: updatedIngredientsAdded,
        price: state.price + INGREDIENTS_PRICES[action.igType]
    });
}

const removeIngredient = (state, action) => {
    if (state.ingredients[action.igType] > 0) {
        const updatedIngredientsRemoved = updateObject(state.ingredients, {
            [action.igType]: state.ingredients[action.igType] - 1
        });
        return updateObject(state, {
            ingredients: updatedIngredientsRemoved,
            price: state.price - INGREDIENTS_PRICES[action.igType]
        });
    } else {
        return state;
    }
}

const getIngredients = (state, action) => {
    let initialPrice = 4;
    const igArray = [];
    for (let igKey in action.ingredients) {
        igArray.push(INGREDIENTS_PRICES[igKey] * action.ingredients[igKey])
    }
    const addedPrice = igArray.reduce((sum, currentValue) => sum + currentValue , 0);
    initialPrice += addedPrice;

    return updateObject(state, {
        ingredients: action.ingredients,
        price: initialPrice,
        error: false,
        building: false
    });
}

const hasError = (state, action) => {
    return updateObject(state, {
        error: true
    });
}

const buildingBurger = (state, action) => {
    return updateObject(state, {
        building: true
    });
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.INIT_INGREDIENTS: return initIngredients(state, action);
        case actionTypes.ADD_INGREDIENT: return addIngredient(state, action);
        case actionTypes.REMOVE_INGREDIENT: return removeIngredient(state, action);
        case actionTypes.GET_INGREDIENTS: return getIngredients(state, action);
        case actionTypes.HAS_ERROR: return hasError(state, action);
        case actionTypes.BUILDING_BURGER: return buildingBurger(state, action);
        default: return state;
    }
};

export default reducer;


