import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    purchasedOrders: [],
    orders: [],
    loading: false,
    purchased: false
};

const purchaseInit = (state, action) => {
    return updateObject(state, {
        purchased: false
    });
}

const purchaseOrderStart = (state, action) => {
    return updateObject(state, {
        loading: true
    });
}

const purchaseOrderSuccess = (state, action) => {
    const newOrder = updateObject(action.orderData, {
        id: action.orderId
    });
    return updateObject(state, {
        purchasedOrders: state.purchasedOrders.concat(newOrder),
        loading: false,
        shouldRedirect: true,
        purchased: true
    });
}

const purchaseOrderFailed = (state, action) => {
    return updateObject(state, {
        loading: false
    });
}

const initOrders = (state, action) => {
    return updateObject(state, {
        orders: [],
        loading: true
    });
}

const setOrders = (state, action) => {
    return updateObject(state, {
        orders: action.orders,
        loading: false
    });
}

const getOrdersFailed = (state, action) => {
    return updateObject(state, {
        loading: false
    });
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.PURCHASE_INIT: return purchaseInit(state, action); 
        case actionTypes.PURCHASE_ORDER_START: return purchaseOrderStart(state, action); 
        case actionTypes.PURCHASE_ORDER_SUCCESS: return purchaseOrderSuccess(state, action); 
        case actionTypes.PURCHASE_ORDER_FAILED: return purchaseOrderFailed(state, action);  
        case actionTypes.INIT_ORDERS: return initOrders(state, action);  
        case actionTypes.SET_ORDERS: return setOrders(state, action);
        case actionTypes.GET_ORDERS_FAILED: return getOrdersFailed(state, action); 
        default: return state;
    }
};

export default reducer;


