import * as actionTypes from './actionTypes';
import axios from '../../axios-orders';

export const purchaseOrderSuccess = (id, orderData) => {
    return {
        type: actionTypes.PURCHASE_ORDER_SUCCESS,
        orderId: id,
        orderData: orderData
    }
}

export const purchaseOrderFailed = (error) => {
    return {
        type: actionTypes.PURCHASE_ORDER_FAILED,
        error: error
    }
}

export const purchaseOrderStart = () => {
    return {
        type: actionTypes.PURCHASE_ORDER_START
    }
}

export const purchaseOrder = (orderData, token) => {
    return dispatch => {
        dispatch(purchaseOrderStart());
        axios.post('orders.json?auth=' + token, orderData)
            .then(response => {
                dispatch(purchaseOrderSuccess(response.data.name, orderData));
            })
            .catch(error => {
                dispatch(purchaseOrderFailed(error));
            });
    }
}

export const purchaseInit = () => {
    return {
        type: actionTypes.PURCHASE_INIT
    }
}

export const initOrders = () => {
    return {
        type: actionTypes.INIT_ORDERS
    }
}

export const setOrders = (orders) => {
    return {
        type: actionTypes.SET_ORDERS,
        orders: orders
    }
}

export const getOrders = (token, userId) => {
    return dispatch => {
        dispatch(initOrders());
        const queryParams = '?auth=' + token + '&orderBy="userId"&equalTo="' + userId + '"';
        axios.get('/orders.json' + queryParams)
            .then(res => {
                const fetchedOrders = [];
                for (let key in res.data) {
                    fetchedOrders.push({
                        ...res.data[key],
                        id: key
                    });
                }
                dispatch(setOrders(fetchedOrders));
            })
            .catch(error => {
                dispatch(getOrdersFailed());
            });
    };
};

export const getOrdersFailed = () => {
    return {
        type: actionTypes.GET_ORDERS_FAILED
    }
}


