import * as actionTypes from './actionTypes';
import axios from '../../axios-orders';


export const initIngredients = () => {
    return {
        type: actionTypes.INIT_INGREDIENTS
    }
};

export const addIngredient = ingredient => {
    return {
        type: actionTypes.ADD_INGREDIENT,
        igType: ingredient
    };
};

export const removeIngredient = ingredient => {
    return {
        type: actionTypes.REMOVE_INGREDIENT,
        igType: ingredient
    };
};

export const syncGetIngredients = (ingredients) => {
    return {
        type: actionTypes.GET_INGREDIENTS,
        ingredients: ingredients
    }
};

export const hasError = () => {
    return {
        type: actionTypes.HAS_ERROR
    }
};

export const getIngredients = () => {
    return dispatch => {
        axios.get('ingredients.json')
            .then(response => {
                dispatch(syncGetIngredients(response.data));
            })
            .catch(error => {
                dispatch(hasError());
            });
    }
};

export const buildingBurger = () => {
    return {
        type: actionTypes.BUILDING_BURGER
    }
};


